import React from "react";

export class Input extends React.Component {
    constructor(props) {
        super(props);
        
        this.props.type === 'submit' ? this.state={onSubmit:this.props.submitHandler} : this.state={onSubmit: null};
    
    }
    render() {
        if (this.props.type == 'textarea') {
            return (<div className={`input-field input-${this.props.type} input-${this.props.name}`} >
                <label htmlFor ={this.props.name}>{this.props.label}</label
                ><textarea id={this.props.name} name={this.props.name} value={this.props.value} onChange={this.props.changeHandler} />
            </div>);
        }
        return (<div className={`input-field input-${this.props.type} input-${this.props.name}`} >
            <label htmlFor= {this.props.name}>{this.props.label}</label>
            <input type={this.props.type} id={this.props.name} name={this.props.name} value={this.props.value} onChange={this.props.changeHandler} onClick={this.state.onSubmit} />
        </div>);
    }
}
